





import { Component, Vue } from 'vue-property-decorator'
import highlight from '../utils/highlight';
@Component
export default class DocumentBody extends Vue {
  mounted() {
    highlight();
  }

  updated() {
    highlight();
    this.$nextTick(() => {
      (this.$refs.documentBody as HTMLDivElement).scrollIntoView()
    })
  }
  
  
}
